import { NgModule } from '@angular/core';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations'; // this is needed!
import { RouterModule } from '@angular/router';
import { HttpModule } from '@angular/http';
import { APP_BASE_HREF } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { CommonModule } from '@angular/common';

import { AppComponent } from './app.component';

import { SidebarModule } from './sidebar/sidebar.module';
import { FooterModule } from './shared/footer/footer.module';
import { NavbarModule} from './shared/navbar/navbar.module';
import { AdminLayoutComponent } from './layouts/admin/admin-layout.component';
import { AuthLayoutComponent } from './layouts/auth/auth-layout.component';

import { AuthGuard } from './guards/auth.guard';
import { AuthGuardAdmin } from './guards/auth.guard.admin';

import { RegisterService } from './services/register.service';
import { LoginService } from './services/login.service';
import { UserService } from './services/user.service';
import { HostService }  from './services/host.service';
import { LocalStorageService }  from './services/local-storage.service';
import { MomentoService } from './services/momento.service'
import { ConteudoService } from './services/conteudo.service'
import { FileService } from './services/file.service'
import { DepoimentosService } from './services/depoimentos.service'

import { AngularVideoplayerComponent } from './angular-videoplayer/angular-videoplayer.component';

import { VgCoreModule } from 'videogular2/core';
import { VgControlsModule } from 'videogular2/controls';
import { GaleriaComponent } from './galeria/galeria.component';
import { ResumoGaleriaComponent } from './resumo-galeria/resumo-galeria.component';

import { CadastroDepoimentosComponent } from './cadastro-depoimentos/cadastro-depoimentos.component';
import { ListaDepoimentoComponent } from './lista-depoimento/lista-depoimento.component';
import { CapaceteComponent } from './capacete/capacete.component';
import { MapaComponent } from './mapa/mapa.component';
import { AngularFontAwesomeModule } from 'angular-font-awesome/angular-font-awesome';


import { MdDatepickerModule, MdInputModule, MdNativeDateModule, MdSelectModule } from '@angular/material';
import { DatepickerModule } from 'angular2-material-datepicker'

import { AppRoutes } from './app.routing';


@NgModule({
    imports:      [
        CommonModule,
        BrowserAnimationsModule,
        FormsModule,
        RouterModule.forRoot(AppRoutes),
        HttpModule,
        SidebarModule,
        NavbarModule,
        FooterModule,
        VgCoreModule,
        VgControlsModule,
        AngularFontAwesomeModule,
        MdDatepickerModule, 
        MdInputModule, 
        MdNativeDateModule, 
        MdSelectModule,
        DatepickerModule
    ],
    declarations: [
        AppComponent,
        AdminLayoutComponent,
        AuthLayoutComponent,
        AngularVideoplayerComponent,
        GaleriaComponent,
        ResumoGaleriaComponent,
        CadastroDepoimentosComponent,
        ListaDepoimentoComponent,
        CapaceteComponent,
        MapaComponent
    ],
    providers: [
        RegisterService,
        LoginService,
        UserService,
        HostService,
        AuthGuard,
        AuthGuardAdmin,
        LocalStorageService,
        MomentoService,
        ConteudoService,
        FileService,
        DepoimentosService
    ],
    bootstrap:    [ AppComponent ]
})
export class AppModule { }



