import {Injectable} from "@angular/core";
import {User} from '../models/user';
import {Momento} from '../models/momento';
import { UserService } from '../services/user.service';




declare interface Componente_Metodo {
	componente?: string;
	metodo?: string;
}

@Injectable()
export class LocalStorageService {
	
	public userLogado: User;  
	public userEditAtual: User;
	public momentoEditAtual: Momento;
	//eliminar substituindo por localstoage pois perde o estado quando autaliza página
	public componente_metodo: Componente_Metodo = {
		componente: '', metodo: ''
	}


	constructor (private userService: UserService) {

		this.userService.getUserByEmail(localStorage.getItem("currentEmail")).subscribe(
			user => {
				this.userLogado = JSON.parse(JSON.parse(JSON.stringify(user))._body);
			}, error => console.log(error)
			);
	}


}



