import { Component, OnInit, Input  } from '@angular/core';
import { Conteudo } from '../models/conteudo';
import { User } from '../models/user';
import { Momento } from '../models/momento';
import { ConteudoService } from '../services/conteudo.service';
import { MomentoService } from '../services/momento.service';
import { UserService } from '../services/user.service';
import { HostService } from '../services/host.service';
import { Router } from '@angular/router';
import { LocalStorageService }  from '../services/local-storage.service';

declare const $: any;

@Component({
  selector: 'app-resumo-galeria',
  templateUrl: './resumo-galeria.component.html',
  styleUrls: ['./resumo-galeria.component.css']
})
export class ResumoGaleriaComponent implements OnInit {

  public conteudos: Conteudo[];
  public conteudosConfigurados: boolean;
  public momento: Momento;

  constructor(private conteudoService: ConteudoService, private momentoService: MomentoService, 
    private hostService: HostService, private router: Router, private userService: UserService, 
    private localStorageService: LocalStorageService) {
    this.conteudosConfigurados = false;
    this.conteudos = [];
    this.momento = new Momento();
  }

  ngOnInit() {

    this.momentoService.getMomentoById(+localStorage.getItem("currentIdMomentoToResumoGallery")).subscribe(
      momentoResponse => {
        this.momento = JSON.parse(JSON.parse(JSON.stringify(momentoResponse))._body);
        this.conteudoService.getConteudosByMomento(this.momento).subscribe(
          conteudosResponse => {
            let ateSeisConteudos: Conteudo[] =JSON.parse(JSON.parse(JSON.stringify(conteudosResponse))._body);

            for(let i=0; i<ateSeisConteudos.length; i++){
              this.conteudos.push(ateSeisConteudos[i]);
              if(i==5)
                break;
            }

          }, error => console.log(error)
          )
      }, error => console.log(error));

  }

  configureGalleryAfterConteudosLoaded(){

    if(!this.conteudosConfigurados){
      this.conteudosConfigurados = true;
    }
  }

/*  getUrlThumbsVideo(){
    return "../assets/img/thumbnail-video.png";

  }*/

  getUrlThumbsVideo(conteudo: Conteudo){
    if(conteudo)
    {
      /*console.log("UrL"+this.hostService.host + this.momentoAtual.urlFoto);*/
      return this.hostService.host + conteudo.url + "#t=0.1";
    }

    return "../assets/img/thumbnail-video.png";

  }


  getUrlConteudo(conteudo: Conteudo){

    if(conteudo)
    {
      return this.hostService.host + conteudo.urlThumbs;
    }

    return "../assets/img/login-imersa.png";

  }

  goToGallery(){
    localStorage.setItem("currentIdMomentoToGallery", this.momento.momentoId.toString());
    this.router.navigate(['/app-galeria']);
  }

  goToTimeline(){
    this.userService.getUserByEmail(localStorage.getItem("currentEmailUserEdit")).subscribe(
      userResponse=> { 
        let user: User = JSON.parse(JSON.parse(JSON.stringify(userResponse))._body);
        this.localStorageService.userEditAtual = user;

        localStorage.setItem("currentEmailUserEdit", user.email);

        this.localStorageService.componente_metodo = { componente:'DataTableComponent', 
        metodo:'goToTimeline'};

        this.router.navigate(['/observador/timeline']);
      }, 
      error => console.log(error)
      );

  }

  goToListaDepoimentos(){
    this.router.navigate(['/app-lista-depoimento']);
  }

}



