import {Injectable} from "@angular/core";
import {Http, Headers} from '@angular/http';
import {Observable}     from 'rxjs/Observable';
import {Conteudo}    from '../models/conteudo';
import {Momento}    from '../models/momento';
import {HostService}  from '../services/host.service';



@Injectable()
export class ConteudoService {

	constructor (private http: Http, private hostService: HostService) {
	}

	saveConteudo(conteudo: Conteudo) {
		let url = this.hostService.host+"rest/super/conteudo/save";
		let headers = new Headers({'Content-Type': 'application/json', 'Authorization': 'Bearer '+localStorage.getItem("token")});
		return this.http.post(url, JSON.stringify(conteudo), {headers: headers});
	}

	getConteudoById(conteudoId: number) {
		let url = this.hostService.host+"rest/super/conteudo/getByID";
		let headers = new Headers({'Content-Type': 'application/json', 'Authorization': 'Bearer '+localStorage.getItem("token")});
		return this.http.post(url, JSON.stringify(conteudoId), {headers: headers});
	}

	getConteudosByMomento(momento: Momento) {
		let url = this.hostService.host+"rest/simple/conteudo/conteudos";
		let headers = new Headers({'Content-Type': 'application/json', 'Authorization': 'Bearer '+localStorage.getItem("token")});
		return this.http.post(url, JSON.stringify(momento), {headers: headers});
	}

	deleteConteudoById(conteudoId: number) {
		let tokenUrl1 = this.hostService.host+"rest/super/conteudo/deleteById";
		let headers1 = new Headers({'Content-Type': 'application/json', 'Authorization': 'Bearer '+localStorage.getItem("token")});
		return this.http.post(tokenUrl1, JSON.stringify(conteudoId), {headers: headers1});
	}



}



