import {Injectable} from "@angular/core";
import {Http, Headers} from '@angular/http';
import {Observable}     from 'rxjs/Observable';
import {Depoimento}    from '../models/depoimento';
import {Momento}    from '../models/momento';
import {User}    from '../models/user';
import {HostService}  from '../services/host.service';



@Injectable()
export class DepoimentosService {

	constructor (private http: Http, private hostService: HostService) {
	}

	saveDepoimento(depoimento: Depoimento) {
		let url = this.hostService.host+"rest/super/depoimento/save";
		let headers = new Headers({'Content-Type': 'application/json', 'Authorization': 'Bearer '+localStorage.getItem("token")});
		return this.http.post(url, JSON.stringify(depoimento), {headers: headers});
	}

	getDepoimentoById(depoimentoId: number) {
		let url = this.hostService.host+"rest/super/depoimento/getById";
		let headers = new Headers({'Content-Type': 'application/json', 'Authorization': 'Bearer '+localStorage.getItem("token")});
		return this.http.post(url, JSON.stringify(depoimentoId), {headers: headers});
	}

	getDepoimentosByUser(user: User) {
		let url = this.hostService.host+"rest/simple/depoimento/depoimentos";
		let headers = new Headers({'Content-Type': 'application/json', 'Authorization': 'Bearer '+localStorage.getItem("token")});
		return this.http.post(url, JSON.stringify(user), {headers: headers});
	}

	deleteDepoimentoById(depoimentoId: number) {
		let tokenUrl1 = this.hostService.host+"rest/super/depoimento/deleteById";
		let headers1 = new Headers({'Content-Type': 'application/json', 'Authorization': 'Bearer '+localStorage.getItem("token")});
		return this.http.post(tokenUrl1, JSON.stringify(depoimentoId), {headers: headers1});
	}



}



