import { Routes } from '@angular/router';

import { AdminLayoutComponent } from './layouts/admin/admin-layout.component';
import { AuthLayoutComponent } from './layouts/auth/auth-layout.component';
import { AuthGuardAdmin } from './guards/auth.guard.admin';
import { AuthGuard } from './guards/auth.guard';


import { AngularVideoplayerComponent } from './angular-videoplayer/angular-videoplayer.component';
import { GaleriaComponent } from './galeria/galeria.component';
import { ResumoGaleriaComponent } from './resumo-galeria/resumo-galeria.component';
import { CadastroDepoimentosComponent } from './cadastro-depoimentos/cadastro-depoimentos.component';
import { ListaDepoimentoComponent } from './lista-depoimento/lista-depoimento.component';
import { CapaceteComponent } from './capacete/capacete.component';
import { MapaComponent } from './mapa/mapa.component';


export const AppRoutes: Routes = [
{
  path: '',
  redirectTo: 'tables/datatables.net',
  pathMatch: 'full',
  canActivate: [AuthGuardAdmin]
},
{
  path: '',
  component: AdminLayoutComponent,
  children: [
  {
    path: '',
    loadChildren: './dashboard/dashboard.module#DashboardModule',
    canActivate: [AuthGuardAdmin]
  }, {
    path: 'components',
    loadChildren: './components/components.module#ComponentsModule'
  }, {
    path: 'forms',
    loadChildren: './forms/forms.module#Forms',
    canActivate: [AuthGuardAdmin]
  }, {
    path: 'tables',
    loadChildren: './tables/tables.module#TablesModule',
    canActivate: [AuthGuardAdmin]
  },
  {
    path: '',
    loadChildren: './timeline/timeline.module#TimelineModule'
  },
  {
    path: 'app-angular-videoplayer',
    component: AngularVideoplayerComponent,
    canActivate: [AuthGuard]
  }, {
    path: 'app-galeria',
    component: GaleriaComponent,
    canActivate: [AuthGuard]
  }, {
    path: 'app-resumo-galeria',
    component: ResumoGaleriaComponent,
    canActivate: [AuthGuard]
  }, {
    path: 'app-cadastro-depoimentos',
    component: CadastroDepoimentosComponent,
    canActivate: [AuthGuard]
  }, {
    path: 'app-lista-depoimento',
    component: ListaDepoimentoComponent,
    canActivate: [AuthGuard]
  },{
    path: 'app-capacete',
    component: CapaceteComponent,
    canActivate: [AuthGuard]
  },{
    path: 'app-mapa',
    component: MapaComponent,
    canActivate: [AuthGuard]
  },

    /*, {
        path: 'maps',
        loadChildren: './maps/maps.module#MapsModule'
    }, {
        path: 'widgets',
        loadChildren: './widgets/widgets.module#WidgetsModule'
    }, {
        path: 'charts',
        loadChildren: './charts/charts.module#ChartsModule'
    }, {
        path: 'calendar',
        loadChildren: './calendar/calendar.module#CalendarModule'
    }, {
        path: '',
        loadChildren: './userpage/user.module#UserModule'
    }, {
        path: '',
        loadChildren: './timeline/timeline.module#TimelineModule'
      }*/
      ]
    },
    {
      path: '',
      component: AuthLayoutComponent,
      children: [{
        path: 'pages',
        loadChildren: './pages/pages.module#PagesModule'
      }]
    }
    ];



