import { Component, OnInit, AfterViewInit, Input } from '@angular/core';
import { Conteudo } from '../models/conteudo';
import { Momento } from '../models/momento';
import { ConteudoService } from '../services/conteudo.service';
import { MomentoService } from '../services/momento.service';
import { HostService } from '../services/host.service';
import { UserService } from '../services/user.service';
import { Router } from '@angular/router';

declare const $: any;

@Component({
  selector: 'app-galeria',
  templateUrl: './galeria.component.html',
  styleUrls: ['./galeria.component.css']
})
export class GaleriaComponent implements OnInit, AfterViewInit {

  public conteudos: Conteudo[];
  public momentoAtual: Momento;
  public conteudosConfigurados: boolean;
  public videoPlayingFlag: boolean;

  constructor(private conteudoService: ConteudoService, private momentoService: MomentoService, private userService: UserService,
    private hostService: HostService, private router: Router) { 
    this.conteudosConfigurados = false;
    this.videoPlayingFlag = false;
  }

  ngOnInit() {


    this.momentoService.getMomentoById(+localStorage.getItem("currentIdMomentoToGallery")).subscribe(
      momentoResponse => { 
        this.momentoAtual = JSON.parse(JSON.parse(JSON.stringify(momentoResponse))._body);
        this.conteudoService.getConteudosByMomento(this.momentoAtual).subscribe(
          conteudosResponse => { 
            this.conteudos = JSON.parse(JSON.parse(JSON.stringify(conteudosResponse))._body);
            console.log("Conteudo da Galeria:");
            console.log(this.conteudos);
          }, error => console.log(error)
          )
      }, error => console.log(error));

  }

  showHide(el: any) {
    var display = document.getElementById(el).style.display;
    if(display == "none")
      document.getElementById(el).style.display = 'block';
    else
      document.getElementById(el).style.display = 'none';
  }

  configureThumbsAndLarges(){
    $(document).ready(function(){
      $("[class^='thumbnail-']").click(function(){
        $("[class^='thumbnail-']").slideToggle("fast");
        $(this).next("[class^='large-']").slideToggle();
      });

      $(".close").click(function(){
        $("[class^='large-']:visible").toggle();
        $("[class^='thumbnail-']").fadeToggle("fast");;
      });

    });

  }

  configureGalleryAfterConteudosLoaded(){

    if(!this.conteudosConfigurados){
      this.configureThumbsAndLarges();
      this.conteudosConfigurados = true;
    }
  }

  playVideoFlag(){
    this.videoPlayingFlag = true;
  }

  stopVideo(id: string){
    if(this.videoPlayingFlag){
      document.getElementById(id).click();
      this.videoPlayingFlag = false;
    }
  }

  ngAfterViewInit(){
    this.configureThumbsAndLarges();

/*      this.testeVideo1 = document.getElementById('0').click();
*/    

}

getUrlThumbsVideo(conteudo: Conteudo){
  if(conteudo)
  {
    /*console.log("UrL"+this.hostService.host + this.momentoAtual.urlFoto);*/
    return this.hostService.host + conteudo.url + "#t=0.1";
  }

   return "../assets/img/thumbnail-video.png";

}



getUrlConteudo(conteudo: Conteudo){
  
    if(conteudo)
    {
      /*console.log("UrL"+this.hostService.host + this.momentoAtual.urlFoto);*/
      return this.hostService.host + conteudo.url;
    }
  
    return "../assets/img/login-imersa.png";
  
}

getUrlThumbsConteudo(conteudo: Conteudo){

  if(conteudo)
  {
    /*console.log("UrL"+this.hostService.host + this.momentoAtual.urlFoto);*/
    return this.hostService.host + conteudo.urlThumbs;
  }

  return "../assets/img/login-imersa.png";

}


goToResumoGallery(momento: Momento){
  localStorage.setItem("currentIdMomentoToResumoGallery", this.momentoAtual.momentoId.toString());
  localStorage.setItem("currentEmailUserEdit", this.momentoAtual.user.email);
  this.router.navigate(['/app-resumo-galeria']);
}

}



