import {Injectable} from "@angular/core";
import {Http, Headers} from '@angular/http';
import {Observable}     from 'rxjs/Observable';
import {Momento}    from '../models/momento';
import {User}    from '../models/user';
import {HostService}  from '../services/host.service';


@Injectable()
export class MomentoService {

	constructor (private http: Http, private hostService: HostService) {}

	saveMomento(momento: Momento) {
		let url = this.hostService.host+"rest/super/momento/save";
		let headers = new Headers({'Content-Type': 'application/json', 'Authorization': 'Bearer '+localStorage.getItem("token")});
		return this.http.post(url, JSON.stringify(momento), {headers: headers});
	}

	getMomentosByUser(user: User) {
		let url = this.hostService.host+"rest/simple/momento/momentos";
		let headers = new Headers({'Content-Type': 'application/json', 'Authorization': 'Bearer '+localStorage.getItem("token")});
		return this.http.post(url, JSON.stringify(user), {headers: headers});
	}

	getMomentoById(momentoId: number) {
		let url = this.hostService.host+"rest/simple/momento/getById";
		let headers = new Headers({'Content-Type': 'application/json', 'Authorization': 'Bearer '+localStorage.getItem("token")});
		return this.http.post(url, JSON.stringify(momentoId), {headers: headers});
	}

	deleteMomentoById(momentoId: number) {
		let tokenUrl1 = this.hostService.host+"rest/super/momento/deleteById";
		let headers1 = new Headers({'Content-Type': 'application/json', 'Authorization': 'Bearer '+localStorage.getItem("token")});
		return this.http.post(tokenUrl1, JSON.stringify(momentoId), {headers: headers1});
	}


}



