import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import {Depoimento}    from '../models/depoimento';
import {User}    from '../models/user';
import {DepoimentosService}  from '../services/depoimentos.service';
import {UserService}  from '../services/user.service';
import { HostService} from '../services/host.service'

declare const $: any;

@Component({
  selector: 'app-lista-depoimento',
  templateUrl: './lista-depoimento.component.html',
  styleUrls: ['./lista-depoimento.component.css']
})

export class ListaDepoimentoComponent implements OnInit {

  public depoimentos: Depoimento = new Depoimento();

  public user: User = new User();

  public emailLocal: string;

  public depoimentoIdParaDeletar: string;

  constructor(private router: Router, private depoimentosService: DepoimentosService, private userService: UserService,
    private hostService: HostService) { }

  ngOnInit() {
    this.emailLocal = localStorage.getItem("currentEmailUserEdit");

    this.listarDepoimentos();
  }

  listarDepoimentos() {

    this.userService.getUserByEmail(localStorage.getItem("currentEmailUserEdit")).subscribe(
            userResponse => {

                this.user = JSON.parse(JSON.parse(JSON.stringify(userResponse))._body);;

                this.depoimentosService.getDepoimentosByUser(this.user).subscribe(
                  data => {
                    this.depoimentos = JSON.parse(JSON.parse(JSON.stringify(data))._body);
                    console.log(this.depoimentos);
                  },
                  error => console.log(error)
                );
            },
            error => console.log(error)
    );
  }


  goToCadastroDepoimentos(){
    this.router.navigate(['/app-cadastro-depoimentos']);
  	this.router.navigate(['/app-cadastro-depoimentos']);

  }
   goToTimeline(){
     localStorage.setItem("currentEmailUserEdit" , this.emailLocal );
      this.router.navigate(['/observador/timeline']);
   }


   getUrlThumbs(i: any){
      return this.hostService.host + this.depoimentos[+i].urlAvatarThumbs;
   }

   getUrlConteudo(i: any){
    return this.hostService.host + this.depoimentos[+i].urlConteudo;
 }

    iAdmin(): Boolean{
      let isAdmin = localStorage.getItem("currentIsAdmin");

      if(isAdmin == 'true')
        return true;

      return false;
    }

  deleteDepoimento(){
    console.log(this.depoimentoIdParaDeletar);
    this.depoimentosService.deleteDepoimentoById(+this.depoimentoIdParaDeletar).subscribe(
        success => {
            this.showNotification('top','right', "Depoimento apagado com Sucesso!", 2);
            location.reload();
        }, error => console.log(error) )

  }

  setReferenciaDepoimentoADeletar(depoimentoId: string ){
      this.depoimentoIdParaDeletar = depoimentoId;
  }

      showNotification(from: any, align: any, message: string, color: number) {

        const type = ['', 'info', 'success', 'warning', 'danger', 'rose', 'primary'];

        /*const color = Math.floor((Math.random() * 6) + 1);*/

        $.notify({
            icon: 'notifications',
            message: message,
        }, {
            type: type[color],
            timer: 7000,
            delay: 10000,
            placement: {
                from: from,
                align: align
            },
            onClose: null
        });
    }

    editDepoimento(depoimentoId: string) {
      localStorage.setItem("currentIdDepoimentoEdit" , depoimentoId);
      this.router.navigate(['/forms/edicao-depoimento']);
    }
          

}



