import { TipoEvento } from './tipo_evento'

export class User {
	public userId: number;
	public nome: string;
	public sobrenome: string;
	public email: string;
	public password: string;
	public created: Date;
	public isAdmin: boolean = true;
	public tipoEvento: TipoEvento = TipoEvento.casamento;
	public urlFoto: string;
	public urlThumbsFoto: string;
	public urlfundo: string;
}


