import {Injectable} from "@angular/core";
import {Http, Headers} from '@angular/http';
import {Observable}     from 'rxjs/Observable';
import {User}    from '../models/user';
import {UserService}  from '../services/user.service';
import {HostService}  from '../services/host.service';



@Injectable()
export class LoginService {
  token: string;
  user: User;
  userLogado: User = new User();
  private isAdminLocal: boolean;
  private isSuperAdminLocal: boolean;

  constructor (private http: Http, private userService: UserService, private hostService: HostService) {
    this.isAdminLocal = false;
  }

  sendCredential(model: any) {
    let tokenUrl1 = this.hostService.host+"login";
    let headers1 = new Headers({'Content-Type': 'application/json'});
    return this.http.post(tokenUrl1, JSON.stringify(model), {headers: headers1});
  }

  sendToken(token: any) {
    let tokenUrl2 = this.hostService.host+"rest/simple/user/token";
    let getHeaders = new Headers({'Authorization':'Bearer '+token});

    return this.http.get(tokenUrl2, {headers: getHeaders})
  }

  logout() {
    localStorage.setItem("token","");
    localStorage.setItem("currentUserName", '');
    localStorage.setItem("currentIsAdmin", '');
  }

  checkLogin() {
    if (localStorage.getItem("currentEmail")!=null && localStorage.getItem("currentEmail")!='' && localStorage.getItem("token")!=null && localStorage.getItem("token")!='') {
      return true;
    } else {
      return false;
    }
  }

  checkAdmin(){
      if (localStorage.getItem("currentIsAdmin")=="true" && this.checkLogin()) {
      return true;
    } else {
      return false;
    }

  }

  checkAdminObservable(email: string) {
    let tokenUrl = this.hostService.host+"rest/simple/user/email";
    let headers = new Headers({'Content-Type': 'application/json', 'Authorization': 'Bearer '+localStorage.getItem("token")});
    return this.http.post(tokenUrl, email, {headers: headers}).map( 

      userResponse => {
        let user: User = JSON.parse(JSON.parse(JSON.stringify(userResponse))._body);
        return (user.isAdmin && this.checkLogin());
      });

  }

}



